function knopAjax(knop, type) {
    if (knop.hasClass('confirm') && !confirm(knop.attr('title') + '.\n\nWeet u het zeker?')) {
        modalClose();
        return false;
    }
    var source = knop;
    var done = domUpdate;
    var data = knop.attr('data');
    if (knop.hasClass('popup')) {
        source = false;
    }
    if (knop.hasClass('prompt')) {
        data = data.split('=');
        var val = prompt(data[0], data[1]);
        if (!val) {
            return false;
        }
        data = encodeURIComponent(data[0]) + '=' + encodeURIComponent(val);
    }
    if (knop.hasClass('addfav')) {
        data = {
            tekst: document.title.replace('C.S.R. Delft - ', ''),
            link: window.location.href,
        };
    }
    if (knop.hasClass('DataTableRowKnop')) {
        var dataTableId_1 = knop.parents('table').attr('id');
        data = {
            'DataTableId': dataTableId_1,
            'DataTableSelection[]': [knop.parents('tr').attr('data-uuid')],
        };
        done = function (response) {
            if (typeof response === 'object') {
                fnUpdateDataTable('#' + dataTableId_1, response);
                if (response.modal) {
                    modalOpen(response.modal);
                    init(document.querySelector('#modal'));
                }
                else {
                    modalClose();
                }
            }
            else {
                domUpdate(response);
            }
        };
    }
    if (knop.hasClass('DataTableResponse')) {
        var tableId_1 = knop.attr('data-tableid');
        if (!document.getElementById(tableId_1)) {
            tableId_1 = knop.closest('form').attr('data-tableid');
            if (!document.getElementById(tableId_1)) {
                alert('DataTable not found');
            }
        }
        data = {
            'DataTableId': tableId_1,
            'DataTableSelection[]': fnGetSelection('#' + tableId_1),
        };
        done = function (response) {
            if (typeof response === 'object') {
                fnUpdateDataTable('#' + tableId_1, response);
                if (response.modal) {
                    modalOpen(response.modal);
                    init(document.querySelector('#modal'));
                }
                else {
                    modalClose();
                }
            }
            else {
                domUpdate(response);
            }
        };
        if (!knop.hasClass('SingleRow')) {
            source = false;
        }
    }
    if (knop.hasClass('ReloadPage')) {
        done = reload;
    }
    else if (knop.hasClass('redirect')) {
        done = redirect;
    }
    ajaxRequest(type, knop.attr('href'), data, source, done, alert);
}
export function knopPost(event) {
    event.preventDefault();
    var target = event.target;
    if ($(target).hasClass('range')) {
        if ((target).tagName.toUpperCase() === 'INPUT') {
            takenSelectRange(event);
        }
        else {
            takenSubmitRange(event);
        }
        return false;
    }
    knopAjax($(this), 'POST');
    return false;
}
function knopGet(event) {
    event.preventDefault();
    knopAjax($(this), 'GET');
    return false;
}
function knopVergroot(event) {
    var knop = $(this);
    var id = knop.attr('data-vergroot');
    var oud = knop.attr('data-vergroot-oud');
    if (oud) {
        $(id).animate({ height: oud }, 600);
        knop.removeAttr('data-vergroot-oud');
        knop.find('span.fa').removeClass('fa-compress').addClass('fa-expand');
        knop.attr('title', 'Uitklappen');
    }
    else {
        knop.attr('title', 'Inklappen');
        knop.find('span.fa').removeClass('fa-expand').addClass('fa-compress');
        knop.attr('data-vergroot-oud', $(id).height());
        $(id).animate({
            height: $(id).prop('scrollHeight') + 1,
        }, 600);
    }
}
